.task .task-description { white-space: pre-line; font-size: 14px; color: #8993ad; padding: 10px 20px 20px 20px; border-bottom: 1px solid #f5f5f5;}
.name-task { font-size: 16px;  padding: 20px 20px; border-bottom: 1px solid #f5f5f5; display: flex; align-items: center; }

